* {
  box-sizing: border-box;
}

html {
  font-family: sans-serif;
}

body {
  margin: 0;
}

.goal-item {
  position: relative;
  margin: 1rem 0;
  background: #8b005d;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
  color: white;
  padding: 1rem 2rem;
  -webkit-animation: scale-fade-in 0.4s ease-in-out;
          animation: scale-fade-in 0.4s ease-in-out;
  transition: opacity 0.3s ease-out, -webkit-transform 0.3s ease-out;
  transition: transform 0.3s ease-out, opacity 0.3s ease-out;
  transition: transform 0.3s ease-out, opacity 0.3s ease-out, -webkit-transform 0.3s ease-out;
  border-radius: 7px;
}

.icon {
  cursor: pointer;
  position: absolute;
  color: white;
  right: 4%;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  font-size: 22px;
  background-color: #8ec5fc;
  border-radius: 50%;
  width: 25px;
  height: 25px;
  -webkit-animation: rotate-fade-out 2s infinite;
          animation: rotate-fade-out 2s infinite;
  padding: 2px;
}
.icon.clicked {
  background-color: #ee7501;
}
.goal-item.clicked {
  transition: all 0.4s ease-out;
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
  opacity: 0;
}
.goal-item:not(.clicked) {
  transition: all 0.3s ease-out;
}
@-webkit-keyframes scale-fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes scale-fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@-webkit-keyframes rotate-fade-out {
  0% {
    -webkit-transform: translateY(-50%) scale(0.9);
            transform: translateY(-50%) scale(0.9);
  }
  50% {
    -webkit-transform: translateY(-50%) scale(1.2);
            transform: translateY(-50%) scale(1.2);
  }
  100% {
    -webkit-transform: translateY(-50%) scale(0.9);
            transform: translateY(-50%) scale(0.9);
  }
}

@keyframes rotate-fade-out {
  0% {
    -webkit-transform: translateY(-50%) scale(0.9);
            transform: translateY(-50%) scale(0.9);
  }
  50% {
    -webkit-transform: translateY(-50%) scale(1.2);
            transform: translateY(-50%) scale(1.2);
  }
  100% {
    -webkit-transform: translateY(-50%) scale(0.9);
            transform: translateY(-50%) scale(0.9);
  }
}

.goal-list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.Button_button__2v76E {
  font: inherit;
  padding: 0.5rem 1.5rem;
  border: 1px solid #8b005d;
  color: white;
  background: #8b005d;
  box-shadow: 0 0 4px rgba(0, 0, 0, 0.26);
  cursor: pointer;
