#goals {
  width: 35rem;
  max-width: 90%;
  margin: 3rem auto;
}

#goal-form {
  width: 30rem;
  max-width: 90%;
  margin: 3rem auto;
  padding: 2rem;
  box-shadow: 0 2px 12px rgba(0, 0, 0, 0.2);
  border-radius: 10px;
}

html {
  height: 100%;
  margin: 0;
  padding: 0;
  background-color: #97d9e1;
  background-image: linear-gradient(
    225deg,
    #97d9e1 0%,
    #d9afd9 58%,
    #8b005d 100%
  );
  background-color: #8ec5fc;
  background-image: linear-gradient(62deg, #8ec5fc 0%, #e0c3fc 100%);
  font-family: "Courier New", Courier, monospace;
  font-size: 18px;
  overflow-x: hidden;
}

::selection {
  background-color: #e0c3fc;
  color: black;
}

